<template>
    <div class="login">
        <a-row class="mt-60">
            <a-col :span="12">
                <img src="../assets/temp04.png" alt="img" width="500">
            </a-col>
            <a-col :span="6">
                <a-form id="components-form-demo-normal-login" :form="form" class="login-form t-left" @submit="handleSubmit">
                    <h3>Login</h3>

                    <a-form-item>
                        <a-input v-decorator="[
                            'Email',
                            { rules: [
                            { type: 'email', message: 'The input is not valid E-mail!' },
                            { required: true, message: 'Please input your Email!' }
                            ] } ]" placeholder="Email">
                            <a-icon slot="prefix" type="mail" style="color: rgba(0,0,0,.25)" />
                        </a-input>
                    </a-form-item>

                    <a-form-item>
                        <a-input v-decorator="[
                            'Password',
                            { rules: [{ required: true, message: 'Please input your Password!' }, { validator: validateToNextPassword }] },
                            ]" type="password" placeholder="Password">
                            <a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
                        </a-input>
                    </a-form-item>

                    <a-form-item>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                <a-checkbox v-decorator="[
                                    'Remember',
                                    {
                                        valuePropName: 'checked',
                                        initialValue: true,
                                    },
                                    ]">
                                    Remember me
                                </a-checkbox>
                            </a-col>
                            <a-col :span="12">
                                <router-link class="login-form-forgot" to="/forgot-password">Forgot password</router-link>
                            </a-col>
                        </a-row>
                    </a-form-item>
                    <a-form-item>
                        <a-row>
                            <a-button :loading="loading" type="primary" html-type="submit" class="login-form-button">Login</a-button>
                        </a-row>
                        <a-row class="t-left">
                            <router-link to="/register">Register now!</router-link>
                        </a-row>
                    </a-form-item>
                </a-form>
            </a-col>
        </a-row>

        <div style="margin-top: 500px;"></div>
    </div>
</template>


<script>
export default {
    name: "Login",
    components: {},
    data() {
        return {
            loading: false,
        };
    },
    beforeCreate() {
        this.form = this.$form.createForm(this, { name: "normal_login" });
    },
    methods: {
        handleSubmit(e) {
            e.preventDefault();
            this.form.validateFields((err, values) => {
                if (!err) {
                    console.log("Received values of form: ", values);

                    this.loading = true;
                    $.ajax({
                        url: `${this.g.apiUrl}/api/login`,
                        data: values,
                        type: "post",
                        error: (XMLHttpRequest, textStatus, errorThrown) => {
                            this.loading = false;
                            console.log(
                                XMLHttpRequest,
                                textStatus,
                                errorThrown
                            );
                            alert(XMLHttpRequest.responseJSON.message);
                        },
                        success: (res) => {
                            this.loading = false;
                            if (res.flag != "ok") {
                                alert(res.flag);
                                return;
                            }
                            this.$EventBus.$emit("loginComplete", res.data);
                            this.goMyHome();
                        },
                    });
                }
            });
        },
        validateToNextPassword(rule, value, callback) {
            const form = this.form;
            if (value && value.length < 6) {
                callback("Must contain 6 or more characters.");
            }
            if (value && this.confirmDirty) {
                form.validateFields(["confirm"], { force: true });
            }
            callback();
        },
        goMyHome() {
            this.$router.push("/my/");
        },
        init() {
            if (window.localStorage) {
                let userInfo = window.localStorage["userInfo"];
                if (userInfo) {
                    this.goMyHome();
                }
            }
        },
    },
    mounted() {
        this.init();
    },
};
</script>

